/*!
 * tabs - 0.2.1 */
.br-tabs {
  border-bottom: 1px solid #cccccc;
  display: flex;
  overflow: auto;
  padding: 0;
  list-style: none;
  margin: 0;
}

.br-tabs.flex-column {
  border-bottom: 0;
}

.br-tabs.flex-column .item {
  border-bottom: 1px solid #cccccc;
  margin: 0;
  padding: 0;
  padding-right: 48px;
}

.br-tabs.flex-column .item a,
.br-tabs.flex-column .item button {
  flex-direction: row;
  padding: 0;
}

.br-tabs.flex-column .item:hover {
  background: #dbe8fb;
}

.br-tabs.flex-column .item.is-active {
  box-shadow: none;
}

.br-tabs.flex-column .item.is-active a:hover,
.br-tabs.flex-column .item.is-active button:hover {
  box-shadow: none;
}

.br-tabs.flex-column .item.is-active .name {
  box-shadow: inset 0 -4px 0 0 #1351b4;
  color: #1351b4;
}

.br-tabs.flex-column .item.is-active .results {
  color: #1351b4;
}

.br-tabs.flex-column .name {
  display: inline-block;
  margin-left: 48px;
  padding: 16px 4px;
  text-align: left;
}

.br-tabs .item {
  display: flex;
  margin: 0 24px;
}

.br-tabs .item a,
.br-tabs .item button {
  align-items: center;
  display: flex;
  flex-direction: column;
  padding: 8px 16px 16px;
  text-decoration: none;
}

.br-tabs .item a:hover,
.br-tabs .item button:hover {
  background: #dbe8fb;
}

.br-tabs .item.is-active {
  box-shadow: inset 0 -4px 0 0 #1351b4;
}

.br-tabs .item.is-active a:hover,
.br-tabs .item.is-active button:hover {
  box-shadow: inset 0 -4px 0 0 #1351b4;
}

.br-tabs .item.is-active .name {
  color: #071d41;
  font-weight: 600;
}

.br-tabs .item.is-active .results {
  color: #1351b4;
  font-weight: 600;
}

.br-tabs .icon {
  align-self: center;
  color: #888888;
  justify-content: center;
  margin: 0 8px;
  width: 32px;
}

.br-tabs .icon + .name {
  margin-left: 0;
}

.br-tabs .name {
  color: #888888;
  flex: 1;
  font-weight: 500;
  text-align: center;
  white-space: nowrap;
}

.br-tabs .results {
  color: #888888;
  font-size: 14px;
  font-weight: 500;
}

.br-tabs a,
.br-tabs button {
  align-items: baseline;
  display: flex;
  flex: 1;
}

.br-tabs a:hover,
.br-tabs button:hover {
  text-decoration: none;
}
